<template>
  <div class="app-layout">
      <app-header></app-header>
      <main class="main">
        <div class="app-body">
          <transition name="fade" mode="out-in">
            <router-view ></router-view>
          </transition>
        </div>
      </main>
      <app-footer></app-footer>
  </div>
</template>
<script>
import AppHeader from './AppHeader'
import AppFooter from './AppFooter'

export default {
  name: 'AppLayout',
  components: {
    AppHeader,
    AppFooter
  },
  mounted () {
    // 先判斷 local storage 有無資料
    if (JSON.parse(localStorage.getItem('vuexList'))) {
      // 抓取 local storage 資料
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      console.log(vuesList)
      // 判斷是否通過第一關
      if (vuesList.isPassPre) {
        // 並把 local storage 所有資料塞入 vuex
        this.$store.commit('equalPower', vuesList.power)
        this.$store.commit('equalCurrentQuestion', vuesList.currentQuestion)
        this.$store.commit('lingWin', vuesList.isLingWin)
        this.$store.commit('getLingCard', vuesList.lingCardType)
        this.$store.commit('fightLing', vuesList.isOnlyFightLing)
        this.$store.commit('kimWin', vuesList.isKimWin)
        this.$store.commit('getKimCard', vuesList.kimCardType)
        this.$store.commit('fightKim', vuesList.isOnlyFightKim)
        this.$store.commit('luluWin', vuesList.isLuluWin)
        this.$store.commit('getLuluCard', vuesList.luluCardType)
        this.$store.commit('fightLulu', vuesList.isOnlyFightLulu)
        this.$store.commit('riverWin', vuesList.isRiverWin)
        this.$store.commit('getRiverCard', vuesList.riverCardType)
        this.$store.commit('fightRiver', vuesList.isOnlyFightRiver)
        this.$store.commit('chooseWhoWin', vuesList.whoWin)
        // 通過就會到 monster compass
        this.$store.commit('changeHeader', true)
        this.$router.push({
          path: './monster-compass'
        })
      }
    } else {
      let vuesList = {
        // 過了前言
        isPassPre: false,
        // 能力值
        power: 6,
        // 問到第幾題
        currentQuestion: 0,
        // 林開郡
        isLingWin: false,
        lingCardType: '',
        isOnlyFightLing: false,
        // 阿勤
        isOnlyFightKim: false,
        isKimWin: false,
        kimCardType: '',
        // lulu
        isLuluWin: false,
        luluCardType: '',
        isOnlyFightLulu: false,
        // river
        isRiverWin: false,
        riverCardType: '',
        isOnlyFightRiver: false,
        // end
        whoWin: ''
      }
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
    }
  }
}
</script>
<style lang="scss" scoped>
.app-layout {
  max-width: 475px;
  margin: auto;

  .main {
    min-height: 100vh;
  }
}

</style>



