<template>
  <footer class="app-footer">
    <div v-if="isShowFooter" @click="showQuestion" class="footer-box">
      <div class="footer-text">能力值<span style="font-size: 24px;">{{power}}</span></div>
    </div>
    <div v-if="isShowQuestion" class="footer-ask-box">
      <div class="footer-ask-count">剩餘題數<br><span style="font-size:30px;">{{ 100-currentQuestion }}</span></div>
      <div class="footer-ask-question">{{ questionAll[currentQuestion].question }}</div>
      <div @click="answer(0)" class="footer-ask-answer">{{ questionAll[currentQuestion].ans1 }}</div>
      <div @click="answer(1)" class="footer-ask-answer">{{ questionAll[currentQuestion].ans2 }}</div>
      <div v-if="isWaitGame" class="footer-ask-go-box">
        <div v-if="isWin" style="color: #95ff00;">恭喜加值！繼續挑戰</div>
        <div v-else style="color: orange;">可惜了！繼續挑戰</div>
        <img @click="nextQuestion" class="footer-ask-go-icon" src="@/assets/images/go-right-arrow.png" alt="go-right-arrow">
      </div>
      <div class="footer-ask-leave-box">
        <img @click="backToCompass" class="footer-ask-leave-icon" src="@/assets/images/go-left-arrow.png" alt="go-left-arrow">
        <div>選擇離開</div>
      </div>
    <div>目前能力值：{{ power }}</div>
    </div>
  </footer>
</template>
<script>

export default {
  name: 'AppFooter',
  components: {

  },
  data () {
    return {
      isShowQuestion: false,
