        { question: '本屆基隆市議員席次是哪個政黨最多？', ans1: '中國國民黨', ans2: '民主進步黨', ans: 0 },
        { question: '基隆市政府沒有哪個單位（處）？', ans1: '觀光及城市行銷處', ans2: '青年事務處', ans: 1 },
        { question: '基隆市政府市民熱線的電話號碼是？', ans1: '1985', ans2: '1999', ans: 1 },
        { question: '基隆市林市長年輕時的照片曾被放上網路，人稱基隆＿＿＿＿？', ans1: '山下智久', ans2: '三浦春馬', ans: 1 },
        { question: '基隆七個行政區不包含哪一區？', ans1: '八堵區', ans2: '信義區', ans: 0 },
        // 11
        { question: '美國隊長曾到訪基隆的哪個造船廠？', ans1: '阿根納 ', ans2: '阿根廷', ans: 0 },
