        { question: '台灣第一座專供民生用水的水庫，是位於基隆暖暖的？', ans1: '西勢水庫', ans2: '新山水庫', ans: 0 },
        { question: '那個地方曾經是水彩畫家倪蔣懷的畫室？', ans1: '許梓桑古厝', ans2: '林開郡洋樓', ans: 1 },
        { question: '陽明文化館的建築體是由日本郵船株式會社於哪一年建造？', ans1: '1866', ans2: '1915', ans: 1 },
