        { question: '哪一條是台灣第一條鐵路專用隧道？', ans1: '五堵台鐵舊隧道', ans2: '獅球嶺隧道', ans: 1 },
        { question: '中元祭的起源是哪兩個族群的人的紛爭？', ans1: '原住民及漢人', ans2: '漳州人及泉州人', ans: 1 },
        // 31
        { question: '曾以「雨信委託行」演出基隆在地故事的劇團是?', ans1: '慾望劇團', ans2: '失望劇團', ans: 0 },
        { question: '由楊三郎創作的「港都夜雨」中的港都是?', ans1: '高雄', ans2: '基隆', ans: 1 },
        { question: '因電影「千禧曼波」而有「舒淇橋」之稱的橋原名是?', ans1: '中正橋', ans2: '中山橋', ans: 1 },
        { question: '出版「畫家帶路，基隆小旅行」一書，畫出故鄉之美的畫家是?', ans1: '王傑', ans2: '王攀元', ans: 0 },
        { question: '無垢舞蹈劇場的作品「醮」是以基隆的什麽素材作為發想?', ans1: '中元祭', ans2: '老鷹', ans: 0 },
        { question: '基隆廟會中鬧熱的傳統樂曲是屬?', ans1: '南管', ans2: '北管', ans: 1 },
        { question: '基隆冷氣很冷的藝文中心是?', ans1: '文化中心', ans2: '陽明海洋文化藝術館', ans: 0 },
        { question: '臺灣紀實攝影的先驅，以紀錄基隆和九份聞名的攝影師是?', ans1: '鄭桑溪', ans2: '鄭春山', ans: 0 },
        { question: '在正濱漁港發起藝術共創的團隊是?', ans1: '星濱山', ans2: '正濱山', ans: 0 },
        { question: '基隆潮藝術的「潮」指的是?', ans1: '潮濕', ans2: '潮汐', ans: 1 },
        // 41
        { question: '基隆市知名景點「兩段式斑馬線」全長70公尺，通行時間有幾秒？', ans1: '41秒', ans2: '43秒', ans: 0 },
        { question: '根據2018主計總處調查，基隆跨縣市通勤人口比例最高，請問為多少？', ans1: '28.7%', ans2: '29.3%', ans: 0 },
        { question: '基隆有兩班台灣好行，其一會經過二砂灣砲台(海門天險），請問是哪一班？', ans1: '龍宮尋寶線', ans2: '濱海奇基線', ans: 1 },
        { question: '基隆-士林的客運號碼?', ans1: ' 9006', ans2: '2088', ans: 0 },
        { question: '基隆市跟新北市交界的火車站？', ans1: '八斗子車站', ans2: '海科站', ans: 0 },
        { question: '歷史最北的火車站是？', ans1: '基隆火車站', ans2: '淡水火車站', ans: 1 },
        { question: '火車縱貫線跟宜蘭縣的分歧點車站是？', ans1: '基隆火車站', ans2: '八堵火車站', ans: 1 },
        { question: '基隆市公車，往和平島的路線是幾號？', ans1: '101', ans2: '201', ans: 0 },
        { question: '站距最短的區間車站是海科館~八斗子，請問站距為？', ans1: '0.5 公里', ans2: ' 0.6公里', ans: 0 },
        { question: '八斗子車站為離海最近的火車站，請問離海距離？', ans1: '15公尺', ans2: '16公尺', ans: 0 },
        // 51
        { question: '以下哪個是基隆市市鳥？', ans1: '黑鳶', ans2: '魚鷹', ans: 0 },
        { question: '以下哪個是基隆市市樹？', ans1: '楓香', ans2: '榕樹', ans: 0 },
        { question: '以下哪個是基隆市市魚？', ans1: '烏魚', ans2: '黑鯛', ans: 1 },
        { question: '以下哪個是基隆市市花？', ans1: '小燕子', ans2: '紫薇', ans: 1 },
        { question: '基隆潮間帶生態豐富，以下哪個「不是」潮間帶常見生物？', ans1: '藤蔓', ans2: '藤壺', ans: 0 },
        { question: '基隆市中心的紅淡山，因為種植許多什麼植物而聞名？', ans1: '紅樹林', ans2: '紅淡比', ans: 0 },
        { question: '基隆知名的「鎖管季」，其中的鎖管，別名又叫什麼？', ans1: '小卷', ans2: '烏賊', ans: 0 },
        { question: '基隆淺山環境較難看到什麼生物？', ans1: '石虎', ans2: '白鼻心', ans: 0 },
        { question: '海洋大學內的龍崗步道以什麼生物聞名？', ans1: '獨角仙', ans2: '螢火蟲', ans: 1 },
        { question: '基隆國家新城的鐘萼木生態公園中，鐘萼木是什麼昆蟲的食草？', ans1: '飛龍白粉蝶', ans2: '臺灣寬尾鳳蝶', ans: 0 },
        // 61
        { question: '基隆市的地勢樣？', ans1: '山多平地少', ans2: '盆地多河川少', ans: 0 },
        { question: '基隆的東西側各被什麼山脈環繞？', ans1: '紅淡山脈及茶壺山脈', ans2: '基隆火山群及五指山山脈', ans: 1 },
        { question: '旭川河是由哪兩條河合併而成？', ans1: '南榮河與西定河', ans2: '田寮河與基隆河', ans: 0 },
        { question: '基隆最近的離島為？', ans1: '基隆嶼', ans2: '和平島', ans: 1 },
        { question: '基隆市七大行政區域中，面積最大的是？', ans1: '七堵區', ans2: '安樂區', ans: 0 },
        { question: '基隆市的平地與丘陵比例為？', ans1: '丘陵95%平地5%', ans2: '丘陵80%平地20%', ans: 1 },
        { question: '基隆第一高山？', ans1: '姜子寮山', ans2: '基隆山', ans: 0 },
        { question: '基隆嶼島上有許多海蝕洞、蜂窩岩等景觀，是因其成型方式為？', ans1: '地震形成', ans2: '火山活動', ans: 1 },
        { question: '基隆紅淡山的海拔高度為？', ans1: '210公尺', ans2: '485公尺', ans: 0 },
        { question: '基隆海邊多為岩岸，假日出遊人擠人的人工沙灘為？', ans1: '外木山沙灘', ans2: '情人海水浴場', ans: 0 },
        // 71
        { question: '基隆慶安宮主祀神明為？', ans1: '王爺', ans2: '媽祖', ans: 1 },
        { question: '哪座廟是基隆市區三大廟之一？', ans1: '天后宮', ans2: '城隍廟', ans: 1 },
        { question: '基隆城隍廟的全名為？', ans1: '護國城隍廟', ans2: '鬥天城隍廟', ans: 0 },
        { question: '基隆中元祭起始日期為？', ans1: '農曆七月一日', ans2: '農曆七月十五', ans: 0 },
        { question: '基隆奠濟宮位於？', ans1: '基隆廟口', ans2: '基隆主普壇', ans: 0 },
        { question: '鬼月時，老一輩叮嚀避免到水邊、山區的原因是？', ans1: '會被好兄弟「抓交替」', ans2: '會被水鬼附身', ans: 0 },
        { question: '鬼月時，為何衣服要再太陽下山前收起來，避免半夜曬衣服？', ans1: '會有孤魂野鬼附身到衣服', ans2: '容易沖到煞氣', ans: 1 },
        { question: '每年何時，基隆會實施大規模交通管制？', ans1: '放水燈', ans2: '普渡', ans: 0 },
        { question: '基隆中元祭由哪個單位主辦？', ans1: '基隆市政府', ans2: '基隆宗親會', ans: 1 },
        { question: '基隆中元祭的普渡桌上，擺放的最後一道菜是？', ans1: '包子', ans2: '空心菜', ans: 1 },
        // 81
        { question: '請問「吉古拉」發音的原文是來自？', ans1: '台語', ans2: '日語', ans: 1 },
        { question: '請問「吉古拉」的原料是什麼？', ans1: '魚漿', ans2: '豆腐', ans: 0 },
        { question: '請問「吉古拉」的形狀比較接近？', ans1: '長方體', ans2: '圓柱體', ans: 1 },
        { question: '請問「吉古拉」的表皮為什麼是金黃色的？', ans1: '煎出來的', ans2: '烤出來的', ans: 1 },
        { question: '請問「吉古拉」的原料「 鱶」指的是什麼？', ans1: '章魚', ans2: '鯊魚', ans: 1 },
        { question: '請問「大腸圈」指的是哪種食物？', ans1: '香腸', ans2: '糯米腸', ans: 1 },
        { question: '請問小吃聚集的「基隆廟口」，指的是哪一間廟？', ans1: '奠濟宮', ans2: '慶安宮', ans: 0 },
        { question: '基隆眾多小吃中的「紅糟」料理，是源自那個地區？', ans1: '漳州', ans2: '福州', ans: 1 },
        { question: '基隆在地小吃中常見的「咖哩」，是哪一國帶來的影響？', ans1: '印度', ans2: '日本', ans: 1 },
        { question: '基隆的咖哩料理，特色是會加入哪種調味料？', ans1: '沙茶醬 ', ans2: ' XO 醬', ans: 0 },
        // 91
        { question: '基隆市境內有幾個台鐵車站？', ans1: '5個', ans2: '8個', ans: 1 },
        { question: '基隆沒有哪家餐飲連鎖店？', ans1: '頂呱呱 ', ans2: '丹丹', ans: 1 },
        { question: '全台跨縣市通勤人口比例最高的縣市是？', ans1: '基隆市', ans2: '新竹市', ans: 0 },
        { question: '以下哪個自然景物沒在基隆範圍內？', ans1: '基隆山', ans2: '基隆河', ans: 0 },
        { question: '「橡皮擦」在基隆又稱為？', ans1: '擦布', ans2: '擦子', ans: 1 },
        { question: '基隆市區最高的建築物是？', ans1: '麗榮皇冠', ans2: '長榮桂冠', ans: 0 },
        { question: '和平島有個「蕃字洞」，這裡的「蕃」是指什麼人？', ans1: '凱達格蘭人', ans2: '荷蘭人', ans: 1 },
        { question: '基隆田寮河上的橋是以什麼為主題？', ans1: '十二生肖', ans2: '十二星座', ans: 0 },
        { question: '在地青年稱為「基隆文化燈塔」的地方是？', ans1: '基隆文化局', ans2: '金豆咖啡-品味迴廊', ans: 1 },
        { question: '基隆沒有哪個行政區？', ans1: '暖暖區', ans2: '冷冷區', ans: 1 }
      ]
    }
  },
  methods: {
    showQuestion () {
      this.isShowQuestion = true
    },
    answer (num) {
      this.isWaitGame = true
      if (num === this.questionAll[this.currentQuestion].ans) {
        this.isWin = true
        // 增加能力值
        if (this.power < 10) {
          this.$store.commit('addPower')
          // 塞入 local storage
          let vuesList = JSON.parse(localStorage.getItem('vuexList'))
          vuesList.power = this.power
          localStorage.setItem('vuexList', JSON.stringify(vuesList))
          //
        }
      } else {
        this.isWin = false
      }
      // 下一題
      this.$store.commit('addCurrentQuestion')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.currentQuestion = this.currentQuestion
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    },
    nextQuestion () {
      this.isWaitGame = false
    },
    backToCompass () {
      this.isShowQuestion = false
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    },
    currentQuestion () {
      return this.$store.state.currentQuestion
    },
    isShowFooter () {
      return this.$store.state.isShowFooter
    }
  }
}
</script>
<style lang="scss" scoped>
.app-footer {
    position: fixed;
    bottom: -10px;
    right: 2vw;
    touch-action: manipulation;
    z-index: 1;

  .footer {

    &-box {
      background-image: url('~@/assets/images/power-icon-bg.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 20px 0px;
      width: 120px;
      height: 60px;
      line-height: 60px;
      color: #95ff00;
      padding: 0px 0px 0px 8px;
      letter-spacing: 3px;
    }

    &-text {
      display: flex;
      align-items: flex-end;
    }
  }

  .footer-ask {

    &-box {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgba(0,0,0,.8);
        color: #95ff00;
    }

    &-count {
      position: fixed;
      top: 20px;
      right: 20px;
      text-align: center;
    }

    &-question {
      margin: 60px 20px 40px;
      text-align: center;
    }

    &-answer {
      width: 100%;
      text-align: center;
      color: white;
      background-color: rgba(149, 255, 0, 0.6);
      padding: 16px 0px;
      margin-top: 16px;
    }

    &-go-box {
      display: flex;
      align-items: center;
      margin-top: 40px;
    }

    &-go-icon {
      width: 40px;
      height: 40px;
      margin-left: 16px;
    }

    &-leave-box {
      width: 100%;
      display: flex;
      align-items: center;
      margin: 40px 0px 0px 60px;
    }

    &-leave-icon {
      width: 40px;
      height: 40px;
      margin-right: 16px;
    }
  }

}

</style>



