import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  // 過了前言
  isPassPre: false,
  // 能力值
  power: 6,
  // 問到第幾題
  currentQuestion: 0,
  // 控制 header 顯示與點點
  isShowHeader: false,
  isDotNote: false,
  isDotCompass: false,
  isDotFrame: false,
  isLingWin: false,
  isShowLingArrow: true,
  // 林開郡卡片種類
  lingCardType: '',
  isOnlyFightLing: false,
  // 控制 能力值 顯示
  isShowFooter: true,
  // 阿勤
  hasPressOverSleep: false,
  goodImpression: 0,
  badImpression: 0,
  kimFinalQuestionsAnswerCorrectNumber: 0,
  isOnlyFightKim: false,
  isKimWin: false,
  kimCardType: '',
  isShowKimArrow: true,
  // lulu
  isLuluWin: false,
  luluCardType: '',
  isOnlyFightLulu: false,
  isShowLuluArrow: true,
  // river
  isRiverWin: false,
  riverCardType: '',
  isOnlyFightRiver: false,
  isShowRiverArrow: true,
  // end
  whoWin: ''
}

const mutations = {
  // 過了第一關沒
  switchPre (state, data) {
    state.isPassPre = data
  },
  // 改變能力值
  addPower (state) {
    state.power = state.power + 1
  },
  changePower (state, data) {
    state.power = state.power - data
  },
  equalPower (state, data) {
    state.power = data
  },
  // 是否顯示能力值
  showFooter (state, data) {
    state.isShowFooter = data
  },
  // 改變當前題目
  addCurrentQuestion (state) {
    state.currentQuestion++
  },
  equalCurrentQuestion (state, data) {
    state.currentQuestion = data
  },
  // 是否顯示 header
  changeHeader (state, data) {
    state.isShowHeader = data
  },
  // 是否點點筆記
  changeNoteDot (state, data) {
    state.isDotNote = data
  },
  // 是否點點羅盤
  changeCompassDot (state, data) {
    state.isDotCompass = data
  },
  // 是否點點圖鑑
  changeFrameDot (state, data) {
    state.isDotFrame = data
  },
  // 林開郡是否終結
  lingWin (state, data) {
    state.isLingWin = data
  },
  // 林開郡的卡片種類
  getLingCard (state, data) {
    state.lingCardType = data
  },
  fightLing (state, data) {
    state.isOnlyFightLing = data
  },
  switchLingArrow (state, data) {
    state.isShowLingArrow = data
  },
  // 阿勤
  pressHasSleepOver (state) {
    state.hasPressOverSleep = true
  },
  addGoodImpression (state) {
    state.goodImpression += 1
  },
  addBadImpression (state) {
    state.badImpression += 1
  },
  addKimFinalQuestionsAnswerCorrectNumber (state) {
    state.kimFinalQuestionsAnswerCorrectNumber += 1
  },
  kimWin (state, data) {
    state.isKimWin = data
  },
  getKimCard (state, data) {
    state.kimCardType = data
  },
  fightKim (state, data) {
    state.isOnlyFightKim = data
  },
  switchKimArrow (state, data) {
    state.isShowKimArrow = data
  },
  // lulu是否終結
  luluWin (state, data) {
    state.isLuluWin = data
  },
  // lulu的卡片種類
  getLuluCard (state, data) {
    state.luluCardType = data
  },
  fightLulu (state, data) {
    state.isOnlyFightLulu = data
  },
  switchLuluArrow (state, data) {
    state.isShowLuluArrow = data
  },
  // river是否終結
  riverWin (state, data) {
    state.isRiverWin = data
  },
  // lulu的卡片種類
  getRiverCard (state, data) {
    state.riverCardType = data
  },
  switchRiverArrow (state, data) {
    state.isShowRiverArrow = data
  },
  fightRiver (state, data) {
    state.isOnlyFightRiver = data
  },
  // 選誰贏
  chooseWhoWin (state, data) {
    state.whoWin = data
  }
}

const actions = {

}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



