<template>
  <header class="app-header">
    <div v-if="isShowHeader" class="header-img-box">
      <router-link :class="[ {'header-animate': isDotNote}]" to="/mission-note">
        <img @click="hideNoteDot" class='header-img' src='@/assets/images/note.png' alt='note'>
      </router-link>
      <router-link :class="[ {'header-animate': isDotCompass}]" to="/monster-compass">
        <img @click="hideCompassDot" class='header-img' src='@/assets/images/compass.png' alt='compass'>
      </router-link>
      <router-link :class="[ {'header-animate': isDotFrame}]" to="/monster-collect">
        <img @click="hideFrameDot" class='header-img' src='@/assets/images/monster-collect.png' alt='monster-collect'>
      </router-link>
    </div>
  </header>
</template>
<script>

export default {
  name: 'AppHeader',
  components: {

  },
  data () {
    return {

    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    hideNoteDot () {
      this.$store.commit('changeNoteDot', false)
    },
    hideCompassDot () {
      this.$store.commit('changeCompassDot', false)
    },
    hideFrameDot () {
      this.$store.commit('changeFrameDot', false)
    }
  },
  computed: {
    isShowHeader () {
      return this.$store.state.isShowHeader
    },
    isDotNote () {
      return this.$store.state.isDotNote
    },
    isDotCompass () {
      return this.$store.state.isDotCompass
    },
    isDotFrame () {
      return this.$store.state.isDotFrame
    }
  }
}
</script>
<style lang="scss" scoped>
.app-header {
  position: fixed;
  top: 20px;
  left: 0px;
  width: 100%;
  z-index: 1;

  .header {

    &-img-box {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 320px;
      margin: auto;
    }

    &-img {
      width: 50px;
      position: relative;
    }

    &-animate {

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        width: 20px;
        height: 20px;
        border-radius: 100%;
        background-color:  #95ff00;
      }
    }
  }
}

@keyframes shakeBox {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(360deg);
  }

  50% {
    transform: rotate(0deg);
  }

  75% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0deg);
  }

}

</style>



