import Vue from 'vue'
import Router from 'vue-router'
import AppLayout from '@/components/layout/AppLayout'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: 'keelung-rpg',
  routes: [
    {
      path: '/',
      component: AppLayout,
      children: [
        {
          path: '/',
          name: 'PageIndex',
          component: () => import('@/pages/Index')
        },
        {
          path: '/mission-note',
          name: 'MissionNote',
          component: () => import('@/pages/MissionNote/Index')
        },
        {
          path: '/monster-collect',
          name: 'MonsterCollect',
          component: () => import('@/pages/MonsterCollect/Index')
        },
        {
          path: '/monster-compass',
          name: 'MonsterCompass',
          component: () => import('@/pages/MonsterCompass/Index')
        },
        {
          path: '/haunted-house-compass',
          name: 'HauntedHouseCompass',
          component: () => import('@/pages/HauntedHouseCompass/Index')
        },
        {
          path: '/haunted-house',
          name: 'HauntedHouse',
          component: () => import('@/pages/HauntedHouse/Index')
        },
        {
          path: '/haunted-house-fight',
          name: 'HauntedHouseFight',
          component: () => import('@/pages/HauntedHouseFight/Index')
        },
        // 通勤
        {
          path: '/commute-arrive-position-point',
          name: 'CommuteArrivePositionPoint',
          component: () => import('@/pages/Commute/ArrivePositionPoint.vue')
        },
        {
          path: '/commute-answer-compass-position',
          name: 'CommuteAnswerCompassPosition',
          component: () => import('@/pages/Commute/AnswerCompassPosition.vue')
        },
        {
          path: '/commute-map-entrance',
          name: 'CommuteMapEntrance',
          component: () => import('@/pages/Commute/MapEntrance.vue')
        },
        {
          path: '/commute-escalator-showup',
          name: 'CommuteEscalatorShowUp',
          component: () => import('@/pages/Commute/EscalatorShowUp.vue')
        },
        {
          path: '/commute-transfer-diagram-introduction',
          name: 'CommuteTransferDiagramIntroduction',
          component: () => import('@/pages/Commute/TransferDiagramIntroduction.vue')
        },
        {
          path: '/commute-south-main-station',
          name: 'CommuteSouthMainStation',
          component: () => import('@/pages/Commute/SouthMainStation.vue')
        },
        {
          path: '/commute-transfer-diagram-one',
          name: 'CommuteTransferDiagramOne',
          component: () => import('@/pages/Commute/TransferDiagramOne.vue')
        },
        {
          path: '/commute-transfer-diagram-two',
          name: 'CommuteTransferDiagramTwo',
          component: () => import('@/pages/Commute/TransferDiagramTwo.vue')
        },
        {
          path: '/commute-transfer-diagram-three',
          name: 'CommuteTransferDiagramThree',
          component: () => import('@/pages/Commute/TransferDiagramThree.vue')
        },
        {
          path: '/commute-terminal-arrived',
          name: 'CommuteTerminalArrived',
          component: () => import('@/pages/Commute/TerminalArrived.vue')
        },
        {
          path: '/commute-train-journey',
          name: 'CommuteTrainJourney',
          component: () => import('@/pages/Commute/TrainJourney.vue')
        },
        {
          path: '/commute-train-audio-guide',
          name: 'CommuteTrainAudioGuide',
          component: () => import('@/pages/Commute/TrainAudioGuide.vue')
        },
        {
          path: '/commute-sanken-station',
          name: 'CommuteSankenStation',
          component: () => import('@/pages/Commute/SankenStation.vue')
        },
        {
          path: '/commute-kim-end',
          name: 'CommuteKimEnd',
          component: () => import('@/pages/Commute/KimEnd.vue')
        },
        {
          path: '/commute-kim-hauntedhouse-fight',
          name: 'CommuteKimHauntedhouseFight',
          component: () => import('@/pages/Commute/KimHauntedHouseFight.vue')
        },
        // lulu
        {
          path: '/lulu-house-compass',
          name: 'LuluHouseCompass',
          component: () => import('@/pages/LuluHouseCompass/Index')
        },
        {
          path: '/lulu-house',
          name: 'LuluHouse',
          component: () => import('@/pages/LuluHouse/Index')
        },
        {
          path: '/lulu-house-fight',
          name: 'LuluHouseFight',
          component: () => import('@/pages/LuluHouseFight/Index')
        },
        // river
        {
          path: '/river-house-compass',
          name: 'RiverHouseCompass',
          component: () => import('@/pages/RiverHouseCompass/Index')
        },
        {
          path: '/river-house',
          name: 'RiverHouse',
          component: () => import('@/pages/RiverHouse/Index')
        },
        {
          path: '/river-house-fight',
          name: 'RiverHouseFight',
          component: () => import('@/pages/RiverHouseFight/Index')
        },
        {
          path: '/river-house-end',
          name: 'RiverHouseEnd',
          component: () => import('@/pages/RiverHouseEnd/Index')
        },
        // end
        {
          path: '/end-talk-one',
          name: 'EndTalkOne',
          component: () => import('@/pages/EndTalkOne/Index')
        },
        {
          path: '/end-talk-two',
          name: 'EndTalkTwo',
          component: () => import('@/pages/EndTalkTwo/Index')
        },
        {
          path: '/end-fight-one',
          name: 'EndFightOne',
          component: () => import('@/pages/EndFightOne/Index')
        },
        {
          path: '/end-fight-two',
          name: 'EndFightTwo',
          component: () => import('@/pages/EndFightTwo/Index')
        },
        {
          path: '*',
          name: '404',
          component: () => import('@/pages/Index')
        }
      ]
    }
  ],
  linkActiveClass: 'active',
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



